@import "../../../styles/variables";

.search-block {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-image: url("../../../images/clouds.png");
  background-size: 50% auto;
  background-repeat: no-repeat;
  background-position: center;

  @media (max-width: $tablets) {
    width: 100%;
  }

  &__btn {
    padding: 1em 2em;
    background-color: $color2;
    border: none;
    border-radius: 10px;
    color: $text-color;
    transition: 0.2s;
    cursor: pointer;
    outline: none;

    &:hover {
      background-color: darken($color2, 5%);
    }
  }

  &__row {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
  }
}

.input-box {
  width: 100%;
  max-width: 500px;
  justify-content: center;
  margin-right: 20px;

  &__input {
    background-color: transparent;
    border: 2px solid $text-color;
    outline: none;
    color: $text-color;
    transition: 0.2s;
    max-width: 500px;
    width: 100%;
    padding: 1em 1em 1em 0.5em;
    border-radius: 1em;
    &:focus {
      border: 2px solid #8691b9;
    }

    &::placeholder {
      color: $text-color;
      font-size: 16px;
    }
  }

  &__suggestions {
    transition: 0.2s;
    max-width: 500px;
    width: 100%;
  }

  &__suggestion {
    transition: 0.2s;
    cursor: pointer;
    color: $text-color;
  }
}
